  $base-color: #3DCFB6;
  $base-color-dark: #08463B;
  $base-background-color: #1C1C1C;
  $base-font: 'Fira Code';
  
  @mixin base-font() {
    font-family: 'Fira Code';
}

@mixin dja(){
  display: flex;
  justify-content: center;
  align-items: center;
}

.button {
  display: inline-block;
  position: relative;
  color: white;
  padding: 12px 24px 12px;
  border: white solid 1px;
  border-radius: 100px;
  font-family: 'Karla';
  font-weight: 400;
  font-size: 18px;
  text-decoration: none;
  text-transform: uppercase;
  user-select: none;
  transition: all 0.2s ease-out;
  overflow: hidden;
  cursor: pointer;
}

.button:hover{
    color: $base-color;
    border: $base-color solid 1px;
  }

.button span::after {
  content: attr(data-text);
  position: absolute;
  left: 230%;
  width: 100%;
  text-align: center;
}

.button:hover .button_inner {
  animation: marqueeAnim 1.5s linear infinite;
  animation-play-state: running;
}

/* Let's create animation */
@keyframes marqueeAnim {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-230%);
  }
}

.box-size {
  width: 568px;
  height: 568px;
}

.box-theme {
  display: flex;
  @include base-font();
  flex-direction: column;
}

.box-label {
  display: flex;
  justify-content: start;
  margin-bottom: 20px;
  font-weight: 500;
  font-size: 24;
  font-family: 'Karla';
  color: #3DCFB6;
}

.article-box {
padding: 32px;
gap: 40px;
border-radius: 30px;
border: 1px 0px 0px 0px;
opacity: 0px;
background-color: #3DCFB61A;
border: 1px solid $base-color;
height: fit-content;
}

.box-header {
  margin-bottom: 40px;
  @include base-font();
  color: $base-color;
  font-family: Fira Code;
  font-size: 64px;
  font-weight: 700;
  line-height: 64px;
  letter-spacing: -0.03em;
  text-align: left;
}

.box-article {
  font-family: 'Karla';
  font-size: 18px;
  font-weight: 400;
  line-height: 21.6px;
  text-align: justify;
  margin-bottom: 32px;
}

.striped-list-line {
  border-color: $base-color !important;
}

.striped-list {
  max-width: 880px;
  border-top: $base-color 1px solid;
  border-bottom: $base-color 1px solid;
}

.striped-list-row {
  max-width: 880px;
  height: 109px;
  padding: 40px 32px 40px 32px;
  justify-content: space-between;
  display: flex;
}